.Briefcase__row {
  margin: 8px -16px;
}

/* Add padding BETWEEN each column (if you want) */
.Briefcase__row,
.Briefcase__row > .Briefcase__column {
  padding: 8px;
  text-align: center;
}

/* Create three equal Briefcase__columns that floats next to each other */
.Briefcase__column {
  /* float: left; */
  display: inline-block;

  width: 44%;
  /* display: none; Hide columns by default */
}

/* Clear floats after rows */
.Briefcase__row:after {
  content: "";
  display: table;
  clear: both;
}

/* Content */
